<template>
  <v-container :style="appStyle" fluid class="custom-router">
    <div class="d-flex justify-end mx-6 align-center my-2">
      <input class="custom-input" v-model="searchQuery" type="text" placeholder="Search..." @input="handleSearch" />
    </div>

    <div v-if="searchResults.length > 0" class="d-sm-flex flex-sm-row mb-12 flex-container">
      <v-card
        v-for="result in searchResults"
        :key="result.article_id"
        tile
        :style="appStyle"
        class="pr-lg-6 pa-6 article-card"
        max-width="450px"
        width="100%"
        min-width="250px"
        height="100%"
        max-height="2000px"
        elevation="0"
        align-center
        justify-center
      >
        <v-img :aspect-ratio="16/9" elevation="0" min-width="50px" max-width="420px" min-height="300px" height="100%" max-height="450px" :src="`https://andrewoethara.my.id/img/${result.thumbnail_image}`"></v-img>

        <div width="100%" height="100%" fluid class="pt-4 pb-2">
          <p class="text-uppercase" style="margin: 0px; font-size: 18px; font-weight: 800;">
            {{ result.title }}
          </p>
        </div>

        <div width="100%" fluid height="100%">
          <p style="font-size: 14px; font-weight: 500;">{{ result.glance }}</p>
        </div>

        <div width="100%" fluid class="py-2">
          <v-btn class="bottom-component" rounded color="#4682A9" elevation="0" dense @click="readFullArticle(result.slug)" height="32px">
            <span style="color: white; font-size: 8.5px; font-weight: 700;">Read</span>
          </v-btn>
        </div>
      </v-card>
    </div>

    <div v-else class="d-sm-flex flex-sm-row mb-12 flex-container">
      <v-card
        v-for="article in articles"
        :key="article.id"
        tile
        :style="appStyle"
        class="pr-lg-6 pa-6 article-card"
        max-width="450px"
        width="100%"
        min-width="250px"
        height="100%"
        max-height="2000px"
        elevation="0"
        align-center
        justify-center
      >
        <v-img :aspect-ratio="16/9" elevation="0" min-width="50px" max-width="420px" min-height="300px" height="100%" max-height="450px" :src="`https://andrewoethara.my.id/img/${article.thumbnail_image}`"></v-img>

        <div width="100%" height="100%" fluid class="pt-4 pb-2">
          <p class="text-uppercase" style="margin: 0px; font-size: 18px; font-weight: 800;">
            {{ article.title }}
          </p>
        </div>

        <div width="100%" fluid height="100%">
          <p style="font-size: 14px; font-weight: 500;">{{ article.glance }}</p>
        </div>

        <div width="100%" fluid class="py-2">
          <v-btn class="bottom-component" rounded color="#4682A9" elevation="0" dense @click="readFullArticle(article.slug)" height="32px">
            <span style="color: white; font-size: 8.5px; font-weight: 700;">Read</span>
          </v-btn>
        </div>
      </v-card>
    </div>
  </v-container>
</template>

<script>
  import axios from "axios";

  export default {
    name: "App",
    data() {
      return {
        articles: [],
        searchQuery: "",
        searchResults: [],
      };
    },
    computed: {
      appStyle() {
        return {
          backgroundColor: "#FFFFFF",
        };
      },
    },
    mounted() {
      this.fetchArticles();
    },
    methods: {
      readFullArticle(slug) {
        this.$router.push({ name: "reading", params: { slug } });
      },
      async fetchArticles() {
        try {
          const response = await axios.get("https://andrewoethara.my.id/get_post.php");
          this.articles = response.data;
        } catch (error) {
          console.error("Error fetching articles:", error);
        }
      },

      handleSearch() {
        axios

          .get("https://andrewoethara.my.id/search_article.php", {
            params: {
              query: this.searchQuery,
            },
          })
          .then((response) => {
            this.searchResults = response.data;
          })
          .catch((error) => {
            console.error("Error:", error);
          });
      },
    },
  };
</script>
