<template>
  <div class="px-6 pb-16" style="height: 100%;">
    <h2>Edit Profil</h2>
    <form @submit.prevent="updateUserProfile">
      <v-row>
        <v-col cols="12" sm="3" class="pr-sm-12 px-3">
          <p class="pt-8" style="font-size: 14px; font-weight: 700;">Photo Profil</p>
          <div>
            <v-img :src="displayedImage" class="mt-4 mx-auto mx-sm-0 mb-8" max-width="250px" aspect-ratio="1" min-width="50px" id="profile_picture_path" v-model="selectedImageFile"></v-img>

            <div>
              <p style="color: #8E8E8E; font-size: 10px; font-weight: 500; height: 20px; margin: 0;">Photo</p>
              <v-file-input
              hide-details="true"
              single-line
              prepend-icon=""
              accept="image/*"
              color="#394867"
              outlined
              label="Upload"
              style="font-size: 10px; font-weight: 400; max-height: 38px !important;"
              class="custom-label-style"
              ref="fileInput"
              @change="onFileChange"
              >
              <template v-slot:selection="{ index, text }">
                <font-awesome-icon color="#4682A9" icon="images" class="px-1" size="xl" />
                <v-chip v-if="index < 2" color="#4682A9" dark label x-small style="font-size: 10px; font-weight: 400;">{{ text }}</v-chip>
              </template>
            </v-file-input>
          </div>
        </div>
      </v-col>

      <v-col class="custom-border pl-sm-12 px-3" cols="12" sm="9" style="height: 100%;">
        <p class="pt-8" style="font-size: 14px; font-weight: 700;">Account Information</p>

        <div>
          <p style="color: #8E8E8E; font-size: 10px; font-weight: 500; height: 20px; margin: 0;">Name</p>

          <input hide-details="true" single-line outlined dense color="#394867" id="name" v-model="userData.name" class="custom-input" style="font-size: 10px !important; font-weight: 400;"/>
        </div>

        <div class="pt-2">
          <p style="color: #8E8E8E; font-size: 10px; font-weight: 500; height: 20px; margin: 0;">Email</p>

          <input hide-details="true" single-line outlined dense color="#394867" id="email" v-model="userData.email"  class="custom-input" style="font-size: 10px !important; font-weight: 400;"/>
        </div>

        <div class="pt-2">
          <p style="color: #8E8E8E; font-size: 10px; font-weight: 500; height: 20px; margin: 0;">About Me</p>

          <textarea
          color="#394867"
          id="about_me"
          v-model="userData.about_me"
          label="Tentang"
          style="font-size: 10px; font-weight: 400; min-height: 170px; padding-top:10.5px;"
          class="custom-area"

          />
        </div>


        <p class="pt-8" style="font-size: 14px; font-weight: 700;">Update Password</p>
        <div>
          <p color="#8E8E8E" style="color: #8E8E8E; font-size: 10px; font-weight: 500; height: 20px; margin: 0;">Old Password</p>

          <input class="custom-input" type="password" v-model="currentPassword" id="currentPassword" />
        </div>

        <div class="pt-4">
          <p style="color: #8E8E8E; font-size: 10px; font-weight: 500; height: 20px; margin: 0;">New Password</p>

          <input class="custom-input" type="password" v-model="newPassword" id="newPassword" />
        </div>


      </v-col>

      <div class="d-flex justify-center mt-16" style="width: 100%;">
        <v-btn style="height: 32px;" rounded type="submit" color="#4682A9" elevation="0">
          <span style="color: white; font-size: 8.5px; font-weight: 800;">Update</span>
        </v-btn>
      </div>
    </v-row>
  </form>
</div>
</template>

<script>
  import axios from "axios";

  export default {
    data() {
      return {
        userData: {
          name: "",
          email: "",
          about_me: "",
        },
        currentPassword:"",
        newPassword:"",
        selectedImageFile: null,
      };
    },
    computed: {
      userProfile() {
        return this.$store.state.user;
      },
      displayedImage() {
        // Use the selected image if available, otherwise use the user's profile picture from the database
        return this.selectedImageFile ? URL.createObjectURL(this.selectedImageFile) : this.userData.profile_picture_path;
      },
    },
    created() {
      // Initialize the userData with the user's data from Vuex
      this.userData = { ...this.$store.state.user };
    },
    methods: {

      updateUserProfile() {
        const formData = new FormData();
        formData.append("author_id", this.$store.state.user.author_id);

        if (this.selectedImageFile) {
          formData.append("profile_picture_path", this.selectedImageFile);
        }

        if (this.userData.name) {
          formData.append("name", this.userData.name);
        }
        if (this.userData.email) {
          formData.append("email", this.userData.email);
        }
        if (this.userData.about_me) {
          formData.append("about_me", this.userData.about_me);
        }

    // Password fields
    formData.append("currentPassword", this.currentPassword);
    formData.append("newPassword", this.newPassword);

    // Make a POST request to update the data
    axios
    .post("https://andrewoethara.my.id/update_user_profile.php", formData)
    .then((response) => {
            // Handle the response if needed
            console.log("Data updated:", response.data);

            // Update Vuex store with the non-empty fields
            this.$store.commit("setUserData", { ...this.$store.state.user, ...this.userData, ...this.selectedImageFile });
          })
    .catch((error) => {
            // Handle errors if the update fails
            console.error("Error updating data:", error);
          });
  },




  async updatePassword() {
        console.log('userData before updatePassword:', this.userData); // Add this line
        console.log('userData.author_id before Axios request:', this.userData.author_id);


        try {
          const response = await axios.post('https://andrewoethara.my.id/update_password.php', {
            author_id: this.$store.state.user.author_id,
            currentPassword: this.currentPassword,
            newPassword: this.newPassword,
          });

          // Handle the response accordingly
          console.log(response.data);
        } catch (error) {
          console.error('Error updating password:', error);
        }
      },





      // Function to handle file input change
      onFileChange(event) {
        this.selectedImageFile = event;
      },
    },
  };
</script>

